<template>
  <div class="container-fluid">
    <h2>Admin Dashboard</h2>
    <div class="row">
      <nav class="navbar navbar-expand-lg navbar-light bg-light">
        <div class="container-fluid">
        <!-- Home and Pending Request Links -->
        <ul class="navbar-nav me-auto mb-2 mb-lg-0">
          <li class="nav-item">
            <router-link class="nav-link" to="/dashboard" style="margin-top: 15px;">Home</router-link>
          </li>
          <li class="nav-item">
            <router-link class="nav-link" to="/pending-requests" style="margin-top: 15px;">Pending Requests</router-link>
          </li>
          <li class="nav-item">
            <router-link class="nav-link" to="/admin-reports" style="margin-top: 15px;">Reports</router-link>
          </li>
          <li>
            <div class="mt-3">
              <form @submit.prevent="searchSections" class="d-flex" align-items-center>
                <input class="form-control me-2" type="search" placeholder="Search" v-model="searchQuery" aria-label="Search" style="height: 40px;">
                <button class="btn btn-outline-primary" type="submit" style="margin-bottom: 10px;">Search</button>
              </form>
            </div>
          </li>
        </ul>
        <!-- Logout Button -->
        <button class="btn btn-outline-danger" @click="logout">Logout</button>
      </div>
      </nav>
    </div>
  </div>
  <div class="row">
    <div class="search-section">  
      <!-- Search Results -->
      <div v-if="searchedSections.length > 0" class="search-results">
        <h2>Search Results</h2>
        <table>
          <thead>
            <tr>
              <th>Category Name</th>
              <th>Category Description</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="section in searchedSections" :key="section.id">
              <td>{{ section.name }}</td>
              <td>{{ section.description }}</td>
            </tr>
          </tbody>
        </table>
      </div>
      <p v-else class="no-results">No results found</p>
    </div>
    
    <!-- Create Category -->
    <div clas="row">Pending Request</div>
    <div class="create-section">
      <h3>Create Category</h3>
      <form @submit.prevent="createSection" class="create-section-form"> 
        <label for="newSectionName">Section Name:</label>
        <input type="text" id="newSection" v-model="newSectionName" required class="create-input" />
        <label for="newSectionDescription">Section Description:</label>
        <input type="text" id="newSectionDescription" v-model="newSectionDescription" class="create-input" />
        <button type="submit" class="create-button">Create Section</button>
      </form>
    </div>

    <!-- Existing Sections -->
    <div class="existing-sections">
      <h3>Existing Sections</h3>
      <ul>
        <li v-for="section in sections" :key="section.id" class="section-item">
          <div class="section-details">
            <span class="section-name">{{ section.name }}</span>
            
          </div>
          <div class="section-actions">
            <button @click="editSection(section.id, section.name, section.description)" class="edit-button">Edit</button>
            <button @click="confirmdelete(section.id)" class="delete-button">Delete</button>
            <button @click="toggleAddingBook(section.id)" class="add-book-button">Add Book</button>
          </div>
          <ul class="book-list">
            <li v-for="book in getBooksForSection(section.id)" :key="book.id" class="book-item">{{ book.name }}</li>
          </ul>
        </li>
        <div v-if="addingBook" class="add-book-section">
          <label for="newBookName"> Enter Book Name</label>
          <input type="text" v-model="newBookName" required class="create-input" />
          <label for="newBookAuthorName">Enter Author Name</label>
          <input type="text" v-model="newBookAuthorName" required class="create-input" />
          <label for="newBookLink">Provide Link for the pdf</label>
          <input type="text" v-model="newBookLink" required class="create-input" />
          <button @click="addBook()" class="add-book-button">Add Book</button>
        </div>
      </ul>
    </div>

    <!-- Edit Category -->
    <div v-if="editingSection" class="edit-section">
      <h3>Edit Category</h3>
      <form>
        <label for="editedSectionName">New Section Name:</label>
        <input type="text" v-model="editedSectionName" required class="edit-input" />
        <label for="editedSectionDescription">New Section Description:</label>
        <input type="text" v-model="editedSectionDescription" class="edit-input" />
        <div class="edit-buttons">
          <button @click="saveEdit" class="save-button">Save</button>
          <button @click="cancelEdit" class="cancel-button">Cancel</button>
        </div>
      </form>
    </div>

    <!-- Confirm Delete -->
    <div v-if="confirmingDelete" class="confirm-delete">
      <h3>Confirm Delete</h3>
      <p>Are you sure you want to delete this section?</p>
      <div class="delete-buttons">
        <button @click="deleteSection" class="confirm-delete-button">Yes, Delete</button>
        <button @click="cancelDelete" class="cancel-delete-button">No, Cancel</button>
      </div>
    </div>
  </div>
</template>

